let storage = window.localStorage

/**
 * @doc setter
 * @description get or query data from localStorage
 * @params {key} {String}
 * @params {value} {String}
 * @return undefined
 * */

const set = function (key, value) {
  value = Object.prototype.toString.call(value) === '[object Object]' && JSON.stringify(value)
  storage.setItem(key, value)
}

/**
 * @doc getter
 * @description set data to localStorage
 * @params {key} {String}
 * @return {value} {String}
 * */

const get = function (key) {
  return storage.getItem(key)
}

/**
 * @doc del
 * @description remove a key and its value in loaclStorage
 * @params {key} {String}
 * @return undefined
 * */

const del = function (key) {
  storage.removeItem(key)
}

 /**
  * @doc del
  * @description clear localStorage
  * @params null
  * @return undefined
  * */

const clear = function () {
  storage.clear()
}

export {
  set,
  get,
  del,
  clear
}



