import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import actions from './actions'

import user from './modules/user'

Vue.use(Vuex)

const state = {
  bottomNav: 'recommend'
}

export default new Vuex.Store({
  state,
  actions,
  mutations,
  modules: {
    user
  }
})



