import { Get, Post, Delete } from './fetch'
import { get as StorageGet } from '@/util/storage'

const info = JSON.parse(StorageGet('USER')) && JSON.parse(StorageGet('USER')).user || ''

const userData = {
  __type: 'Pointer',
  className: '_User',
  objectId: info.objectId
}

export const getSelfTags = async function () {
  const res = await Get('api/classes/Subscribe?&', {
    where: { user: userData },
    include: 'tag',
    limit: '40',
    order: '-createdAt'
  })
  if (res.code === 200) {
    const data = res.data.results
    data.forEach(item => {
      item.btnText = '已关注'
    })
    return { data: data }
  } else {
    return { code: res.code }
  }
}

export const getAllTags = async function (skip) {
  const res = await Get('api/classes/Tag?&', {
    where: {},
    limit: '40',
    skip: skip,
    order: '-subscribersCount'
  })
  if (res.code === 200) return {data: res.data.results}

  return {code: res.code}
}

export const getAllTagsDetail = async function (skip = '') {
  const tags = await getAllTags(skip)
  if (tags.hasOwnProperty('data')) {
    let arr = []
    tags.data.forEach(item => {
      arr.push({
        __type: 'Pointer',
        className: 'Tag',
        objectId: item.objectId
      })
    })
    const res = await Get('api/classes/Subscribe?&', {
      where: {
        tag: {
          '$in': arr
        },
        user: userData
      }
    })
    if (res.code === 200) {
      let subscribes = res.data.results
      tags.data.forEach(all => {
        if (subscribes.length > 0) {
          for (let subscribe of subscribes) {
            if (all.objectId === subscribe.tag.objectId) {
              all.btnText = '已关注'
              break
            } else {
              all.btnText = '关注'
            }
          }
        } else {
          all.btnText = '关注'
        }
      })
      return {data: tags.data}
    } else {
      return {code: res.code}
    }
  }
}

export const getDeleteTagObjectId = async function (objectId) {
  const res = await Get('api/classes/Subscribe?&', {
    where: {
      user: userData,
      tag: {
        __type: 'Pointer',
        className: 'Tag',
        objectId: objectId
      }
    }
  })
  if (res.code === 200) return { data: res.data.results[0].objectId }

  return { code: res.code }
}

export const addTag = async function (objectId) {
  const res = await Post('api/classes/Subscribe?', {
    tag: {
      __type: 'Pointer',
      className: 'Tag',
      objectId: objectId
    },
    user: userData
  })
  return res.code === 201
}

export const deleteTag = async function (objectId) {
  const res = await Delete(`api/classes/Subscribe/${objectId}`)
  return res.code === 200
}



