import fetch from 'isomorphic-fetch'
import promise from 'es6-promise'
import { Toast } from 'mint-ui'
promise.polyfill()

import { encodeSerialize } from './serialize'

/**
 * @name: request
 * @description: basic request fun
 * @params:
 *  type:    String
 *  url:     String
 *  body:    Object
 * */

const request = async function (type, url, body) {
  let params = {
    'method': type,
    'headers': {
      'Content-Type': 'application/json;charset=UTF-8',
      'X-LC-Id': 'mhke0kuv33myn4t4ghuid4oq2hjj12li374hvcif202y5bm6',
      'X-LC-Sign': '6a26ed4b37fad2db51ec91226d20614f,1488613458985',
      'X-LC-Session': 'jjy0ahjfcgf7cthj10bka9o9d'
    },
    'mode': 'cors'
  }

  if (body && type === 'get') url = `${url}${encodeSerialize(body)}`
  if (body && (type === 'post' || type === 'put')) params.body = JSON.stringify(body)
  // use try-catch to catch await error
  try {
    const res = await fetch(url, params)

    const data = { 'data': await res.json() }

    if (res.status === 400) Toast(data.data.error)
    if (res.status === 500) Promise.reject(Toast('Network Error'))

    return {...data, code: res.status}
  } catch (error) {
    throw new Error(error)
  }
}

const Get = async function (url, data) {
  return await request('get', url, data)
}

const Post = async function (url, data) {
  return await request('post', url, data)
}

const Delete = async function (url) {
  return await request('delete', url, null)
}

const Put = async function (url, data) {
  return await request('put', url, data)
}

export {
  Get,
  Post,
  Delete,
  Put
}



