// lazyload
const lazyload = (name) => (resolve) => require([`@/pages/${name}`], resolve)

const routes = [
  {
    path: '/',
    name: 'welcome',
    meta: {title: '掘金'},
    component: lazyload('welcome/welcome')
  },
  {
    path: '/home',
    name: 'home',
    meta: {title: '掘金'},
    component: lazyload('home/home'),
    children: [{
      name: 'recommend',
      path: 'recommend',
      meta: {title: '推荐'},
      component: lazyload('home/recommend')
    }, {
      name: 'front-end',
      path: 'front-end',
      meta: {title: '前端'},
      component: lazyload('home/front-end')
    }, {
      name: 'back-end',
      path: 'back-end',
      meta: {title: '后台'},
      component: lazyload('home/back-end')
    }, {
      name: 'center',
      path: 'center',
      meta: {title: '个人中心'},
      component: lazyload('home/center')
    }]
  },
  {
    name: 'tags',
    path: '/tags',
    meta: {title: '标签管理'},
    component: lazyload('center/tags')
  },
  {
    name: 'profile',
    path: '/profile',
    meta: {title: '个人资料'},
    component: lazyload('center/profile')
  }
]

export default routes



