
export const s20 = function () {
  const RANDOM_DATA = ['1', '2', '3', '4', '5', '6', '7', '8', '9', '0', 'a', 'b', 'c', 'd', 'e', 'f']
  let result = []
  const RANDOM_DATA_LENGTH = RANDOM_DATA.length
  for (let i = 0; i < 20; i++) {
    let index = Math.floor(Math.random() * RANDOM_DATA_LENGTH)
    result.push(RANDOM_DATA[index])
  }
  return result.join('')
}

// change title in wechat and iOS devices
export const changeTitle = function (title) {
  document.title = title
  const iframe = document.createElement('iframe')
  iframe.style.visibility = 'hidden'

  iframe.setAttribute('src', '/favicon.ico')
  const iframeCallback = function () {
    setTimeout(() => {
      iframe.removeEventListener('load', iframeCallback)
      document.body.removeChild(iframe)
    }, 0)
  }
  iframe.addEventListener('load', iframeCallback)
  document.body.appendChild(iframe)
}

/**
 * Why? These methods may be shadowed by properties
 * on the object in question
 * - consider { hasOwnProperty: false }
 * - or, the object may be a null object (Object.create(null)).
 */

export const has = Object.prototype.hasOwnProperty



