import * as types from '@/store/mutation-types'
import { set, get } from '@/util/storage'

let state = {
  user: {},
  isLogin: false
}

state = JSON.parse(get('USER')) || state

const mutations = {
  [types.SET_USER_INFO] (state, res) {
    state.user = res
    state.isLogin = true
    set('USER', state)
  },
  [types.ADD_TAG] (state) {
    state.user.subscribedTagsCount ++
    set('USER', state)
  },
  [types.DELETE_TAG] (state) {
    state.user.subscribedTagsCount --
    set('USER', state)
  }
}

const getters = {
  user: state => state.user,
  isLogin: state => state.isLogin
}

export default {
  state,
  mutations,
  getters
}



