import * as types from './mutation-types.js'

import * as _user from '@/api/user'
import * as _tags from '@/api/tags'

const actions = {
  // login
  async login ({ commit }, data) {
    const res = await _user.login(data)
    res.code === 200 && commit(types.SET_USER_INFO, res.data)
    return res
  },

  // add tag
  async addTag ({ commit }, objectId) {
    const res = await _tags.addTag(objectId)
    res && commit(types.ADD_TAG)
    return res
  },

  // delete tag
  async deleteTag ({ commit }, objectId) {
    const res = await _tags.deleteTag(objectId)
    res && commit(types.DELETE_TAG)
    return res
  }
}

export default actions



